import React, { useContext } from "react";
import MapSelectContext from "../../store/mapSelectContext";
import GroundMap from "./groundMap/GroundMap";
import LavaMap from "./lavaMap/LavaMap";
export default function Maps() {
  const mapCtx = useContext(MapSelectContext);
  return (
    <React.Fragment>
      {mapCtx.map === "ground" && <GroundMap />}
      {mapCtx.map === "lava" && <LavaMap />}
    </React.Fragment>
  );
}
